import React from "react";
import {
    Row,
    Col,
    Card,
    Button,
    FormControl,
    InputGroup
} from "react-bootstrap";

import { Formik } from "formik";
import * as Yup from "yup";
import axios from "axios";

import Aux from "../../../hoc/_Aux";
import MessageModal from "./../../../Components/Alert/Alert";

import i18n from './../../../i18n';

var urlbackend = require("../../../env/env")();
const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;
const SignupSchema = Yup.object().shape({
    email: Yup.string().email("Invalid Email").required("Email is Required"),
    phone_number: Yup.string()
        .matches(phoneRegExp, "Phone number is not valid")
        .required("Phone is Required"),
    city: Yup.string()
        .min(2, "City is Too Short!")
        .max(20, "City is Too Long!")
        .required("City is Required"),
    description: Yup.string()
        .min(2, "description is Too Short!")
        .max(20, "description is Too Long!")
        .required("description is Required"),
    name: Yup.string()
        .min(2, "name is Too Short!")
        .max(20, "name is Too Long!")
        .required("name is Required"),
    code: Yup.string()
        .min(2, "code is Too Short!")
        .max(20, "code is Too Long!")
        .required("code is Required"),
});
class AddAnomalyPickup extends React.Component {
    state = {
        message: [],
        showMessageParams:{
            display:false,
            type:"success",
            message:null
            
        }
    };

    componentDidMount() {
        // axios.get(urlbase+"/AnomalyPickup/list").then(response=>{


        //   console.log(response.data)
        // })


    }
    handleAddRow(e) {
        this.setState({ message: [...this.state.message, ''] })
        console.log('added')
    }
    handleDeleteRow(index) {
        const keys = this.state.message;
        keys.splice(index, 1);
        this.setState({ message: (this.state.message.length > 0 ? keys : []) })
    }
    handleKeyChange(index, e) {
        const pairs = [...this.state.message];

        this.setState({ message: pairs.map((i, ind) => { return ind === index ? e.target.value : i }) })

    }
    handlSubmit = (values) => {
        this.setState({ 
            showMessageParams:{
                display:false,
                type:"success",
                message:null
                
            }
        });
        console.log(this.state.message);

        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Authorization',
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };

        axios.post(urlbackend + "/anomaly/create/", { name: values.name, messages: this.state.message }, config)
            .then(response => {
                this.setState({ 
                    showMessageParams:{
                        display:true,
                        type:"success",
                        message:i18n.t('success-add-anomaly')
                    }
                });
                console.log(response.data)
                this.setState({ message: [] })
            })
            .catch(e => {
                this.setState({ 
                    showMessageParams:{
                        display:true,
                        type:"danger",
                        message:i18n.t('error-add-anomaly')
                    }
                });
                console.log(e)
            })
    };
    showForm = ({
        values,
        errors,
        touched,
        handleChange,
        handleSubmit,
        handleBlur,
        setFieldValue,
        isSubmitting,
        dirty,
        isValid,
        handleReset,
    }) => {
        // console.log(errors);
        const {showMessageParams } = this.state;
        return (
            <Aux>
                <Row>
                    <Col>
                        <Card>
                            <Card.Header>
                                <Card.Title as="h5">{i18n.t('add-pickup-anomaly')}</Card.Title>
                            </Card.Header>
                            {showMessageParams.display==true ?
                            <MessageModal message={showMessageParams.message} design={showMessageParams.type} showit={showMessageParams.display} />
                            :null
                            }
                            <Card.Body>
                                <Row>
                                    <Col md={6}>


                                   {/*      <CustomInput
                                            name="name"
                                            label="name"
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.name}
                                            errors={errors.name}
                                            touched={touched.name}
                                        /> */}

                                        <InputGroup className="mb-3">
                                            <FormControl
                                                as="select"
                                                id="name"
                                                custom
                                                onChange={handleChange}
                                                onBlur={handleBlur}
                                                value={values.name}
                                            >
                                                <option>{i18n.t('choose-anomaly-type')}</option>

                                                {[
                                                    `PICKUP`,
                                                    `DELIVERY`,
                                                    `ANNULATION`
                                                ].map((descriminator, index) => (
                                                    <option key={descriminator} value={descriminator}>
                                                        {descriminator}
                                                    </option>
                                                ))}
                                            </FormControl>
                                        </InputGroup>

                                    </Col>









                                </Row>
                                <Col md={12}>

                                    <Button variant="secondary" onClick={this.handleAddRow.bind(this)}>{i18n.t('add-message')}</Button>
                                </Col>
                                <Row>
                                    {this.state.message.map((value, index) => (

                                        <Col md={6}>
                                            <InputGroup className="mb-3">
                                                <FormControl
                                                    type="text"
                                                    placeholder={i18n.t('message')}
                                                    className="form-control"
                                                    value={this.state.message[index]}
                                                    onChange={this.handleKeyChange.bind(this, index)}
                                                />        <InputGroup.Prepend><Button variant="danger" onClick={this.handleDeleteRow.bind(this, index)}>{i18n.t('delete')} </Button></InputGroup.Prepend>
                                            </InputGroup>
                                        </Col>












                                    ))}

                                </Row>
                                <Button
                                    onClick={() => {
                                        this.handlSubmit(values);
                                        handleReset();
                                        // console.log(values);
                                    }}
                                >
                                    {i18n.t('save')}
                                </Button>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Aux>
        );
    };

    render() {
        return (
            <Formik
                initialValues={{
                    name: "",
                }}
                onSubmit={(values, { setSubmitting }) => {
                    this.submitForm(values);
                    setSubmitting(false);
                }}
                validationSchema={SignupSchema}
            >
                {(props) => this.showForm(props)}
            </Formik>
        );
    }
}

export default AddAnomalyPickup;
